<template>
  <div class="common-detail">
    <div class="content">
      <Form :label-width="120">
          <FormItem label="协助事项：">
            <!-- <CheckboxGroup v-model="watchTableCell.val1">
              <Checkbox v-for="item in assistWorks" :label="item.code" :key="item.code" disabled>{{item.name}}</Checkbox>
            </CheckboxGroup> -->
            <label>
                {{watchTableCell.val1}}
            </label>
          </FormItem>
          <FormItem label="经办人：">
            <label>{{watchTableCell.val2}}</label>
          </FormItem>
          <FormItem label="执行机构：">
            <label>{{watchTableCell.val3}}</label>
          </FormItem>
          <FormItem label="执行省份：">
            <label>{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="执行人：">
            <label>{{watchTableCell.val5}}</label>
          </FormItem>
          <FormItem label="执行人证件号码：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="协查日期：">
            <label>{{watchTableCell.val7}}</label>
          </FormItem>
          <FormItem label="被调查人">
            <!-- ============== -->
            <Row>
              <Row class="flexStart">
                <!-- <Row class="processAddPeople1">被调查人</Row> -->
                <Row class>
                  <Row
                    v-for="(item,index) in addPeopleArr"
                    class="flexStart"
                    :key="index"
                    style="border:1px dashed #E8EAEC"
                  >
                    <Row style="width:70%" class="flexStartWrap">
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户号</p>
                        <Input
                          v-model="item.clientId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">源客户号</p>
                        <Input
                          v-model="item.clientOldId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户名称</p>
                        <Input
                          v-model="item.clientName"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件类型</p>
                        <Input
                          v-model="item.certificateType"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件名称</p>
                        <Input
                          v-model="item.certificateId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                    </Row>
                  </Row>
                </Row>
              </Row>
            </Row>
            <!-- ============== -->
          </FormItem>
          <FormItem label="冻结客户个数：">
            <label>{{watchTableCell.val8}}</label>
          </FormItem>
          <FormItem label="案件类型：">
            <label>{{watchTableCell.val9}}</label>
          </FormItem>
          <FormItem label="涉及金额：">
            <label>{{watchTableCell.val10}}</label>
          </FormItem>
          <FormItem label="交易笔数：">
            <label>{{watchTableCell.val15}}</label>
          </FormItem>
          <FormItem label="业务名称：">
            <label>{{watchTableCell.val12}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val13?'是':'否'}}</label>
          </FormItem>
          <FormItem label="编辑内容：">
            <label>
          <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val4"
                  >
                    <p slot="reference" class="textOverflow disBlock textMaxWidth2">{{watchTableCell.val4}}</p>
                  </el-popover>
              </label>
          </FormItem>
          <FormItem label="附件：">
            <showUsualFiles
              :file-obj="dataObj4"
              :key="fileId4"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加附件：">
            <showUsualFiles
              :file-obj="dataObj5"
              :key="fileId5"
            ></showUsualFiles>
          </FormItem>
          <FormItem label="追加内容：">
            <label>
              <!-- {{watchTableCell.val11}} -->
                  <el-popover
                    placement="top-start"
                    title
                    width="200"
                    trigger="hover"
                    :content="watchTableCell.val11"
                  >
                    <p slot="reference" class="textMaxWidth disBlock textOverflow" >{{watchTableCell.val11}}</p>
                  </el-popover>
              </label>
          </FormItem>
        </Form>
    </div>
  </div>
</template>
<script>
import showUsualFiles from "@/components/commonComp/showUsualFiles";
export default {
  props: ["dataObj"],
  components: {
    showUsualFiles
  },
  data() {
    return {
      addPeopleArr: [], //被调查人
      watchTableCell: {
        val1: "",
        val2: "",
        val3: "",
        val4: "",
        val5: "",
        val6: "",
        val7: "",
        val8: "",
        val9: "",
        val10: "",
        val11: ""
      },
      fileId4: "",
      fileId5: "",
      dataObj4: {},
      dataObj5: {}
    };
  },
  created() {
    let data = this.dataObj.data;
    if (data) {
      this.watchTableCellFun(data, data.busId, data.id);
    }
  },
  methods: {
    watchTableCellFun(val1, val2, val3) {
      this.axios
        .post(`/process/infCaseInvestigation/getEntityRow/${val3}`+'?infViewNum=1')
        .then(response => {
          if (response.code == "200") {
            this.watchTableCell.val1 = response.data.assistWork;
            this.watchTableCell.val2 = response.data.agent;
            this.watchTableCell.val3 = response.data.executeOrg;
            this.watchTableCell.val4 = response.data.executeProvince;
            this.watchTableCell.val5 = response.data.executor;
            this.watchTableCell.val6 = response.data.executorId;
            this.watchTableCell.val7 = response.data.agentTime;
            this.addPeopleArr = response.data.clients;
            this.watchTableCell.val8 = response.data.freezingNum;
            this.watchTableCell.val9 = response.data.caseType;
            this.watchTableCell.val10 = response.data.involvedMoney;
            this.watchTableCell.val15 = response.data.exchangeNum;
            this.watchTableCell.val12 = response.data.businessName;
            this.watchTableCell.val13 = JSON.parse(response.data.ifShow);
            this.watchTableCell.val14 = response.data.content;
            this.dataObj4 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId4 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // -----------------
      let data2 = {
        relationBusId: val2
      };
      data2 = JSON.stringify(data2);
      this.axios
        .post(`/process/infProcessAppend/getInfProcessAppendRows`, data2, {
          headers: { "Content-Type": "application/json" }
        })
        .then(response => {
          if (response.code == "200") {
            this.appendId = response.data.id;
            this.watchTableCell.val11 = response.data.content;
            this.dataObj5 = {
              data: response.data,
              showFlag: false,
              disabled: false
            };
            this.fileId5 = response.data.busId;
          }
        })
        .catch(function(error) {});
      // ---------------
    },
  }
};
</script>


